export default {
	data() {
		return {
			getBackgroundBase64: '',
			showPassword: true,
			iconType: 'success',
			ischecked: false,
			username: '',
			password: '',
			RememberPsd: false,
			useMsg: {},
			platform: ''
		}
	},
	methods: {
		onFocus() {
			// this.$mp.page.$getAppWebview().setStyle({
			// 	softinputNavBar: 'none'
			// })
		},
		onBlur() {
			// this.$mp.page.$getAppWebview().setStyle({
			// 	softinputNavBar: 'auto'
			// })
		},
		changePassword() {
			this.showPassword = !this.showPassword
		},
		clearUserName() {
			console.log('清除');
			this.username = ''
		},
		radioChange: function() {
			this.ischecked = !this.ischecked
			this.RememberPsd = this.ischecked
			console.log('是否记住密码', this.RememberPsd);
		},
		//登录
		async loginFun(username,password) {
			try {
				let data = await this.$wxhttp.get({
					url: '/app/public/login',
					data:{
						username:username,
						password:password
					}
				})
				if( data.code == '0' ){
					uni.setStorageSync('login', JSON.stringify(data.data))
					uni.redirectTo({
						'url': '/pages/index/index'
					})
				}
			} catch (e) {
		
			}
		},
		login: function() {
			if( this.username && this.password ){
				// this.loginFun(this.username,this.password)
				uni.redirectTo({
					'url': '/pages/index/index'
				})
			}else{
				uni.showToast({
					title:"请填写完整！",
					icon:"none"
				})
			}
		}

	},
	onLoad(option) {
		// 判断操作系统
		this.platform = uni.getSystemInfoSync().platform
		let msg = uni.getStorageSync('login')
		if (msg) {
			let username = JSON.parse(uni.getStorageSync('login')).username
			let password = JSON.parse(uni.getStorageSync('login')).password
			if (username) {
				this.username = username
			} else {
				this.username = ''
			}
			if (option.RememberPsd == 'edit') {
				console.log('取消记住密码');
			} else {
				if (password) {
					this.password = password
					this.ischecked = true
					this.RememberPsd = true
				} else {
					this.password = ''
				}
			}
		}
	}
}
